import React from 'react';
import { Link } from 'react-router-dom';
import s from './Header.module.css';

export const Header = () => {
    return (
        <div className={s.header}>
            <div className={s.logo}>logo</div>
            <div className={s.auth}><Link to='/register'>Реєстрація</Link></div>
        </div>
    )
}
