import React, { useState } from 'react';
import {
    createUserWithEmailAndPassword,
    onAuthStateChanged,
    signOut,
    signInWithEmailAndPassword
} from "firebase/auth";
import { auth } from '../firebase-confing';
import s from './Register.module.css';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

function Register() {

    const [registerEmail, setRegisterEmail] = useState("");
    const [registerPassword, setRegisterPassword] = useState("");
    const [loginEmail, setLoginEmail] = useState("");
    const [loginPassword, setLoginPassword] = useState("");

    const [user, setUser] = useState({});

    onAuthStateChanged(auth, (currentUser) => {
        setUser(currentUser);
    })

    const register = async () => {
        try {
            const user = await createUserWithEmailAndPassword(auth, registerEmail, registerPassword);
            console.log(user);
        } catch (error) {
            console.log(error.message);
        }
    }

    const login = async () => {
        try {
            const user = await signInWithEmailAndPassword(auth, loginEmail, loginPassword);
            console.log(user);
        } catch (error) {
            console.log(error.message);
        }
    }

    const logout = async () => {
        await signOut(auth);
    }

    return (
        <div className={s.register}>
            <Tabs className={s.wrapper}>
                <TabList>
                    <Tab>Реєстрація</Tab>
                    <Tab>Вхід</Tab>
                </TabList>

                <TabPanel>
                    <div>
                        <h3>Реєстрація</h3>
                        <input
                            placeholder="Email.."
                            onChange={
                                (e) => {
                                    setRegisterEmail(e.target.value)
                                }
                            } />
                        <input
                            type='password'
                            placeholder="Password..."
                            onChange={(e) => { setRegisterPassword(e.target.value) }} />
                        <button onClick={register}>Зареєструвати</button>
                    </div>
                </TabPanel>
                <TabPanel>
                    <div>
                        <h3>Вхід</h3>
                        <input
                            placeholder="Email.."
                            onChange={
                                (e) => { setLoginEmail(e.target.value) }
                            } />
                        <input
                            type='password'
                            placeholder="Password..." onChange={
                                (e) => { setLoginPassword(e.target.value) }
                            } />
                        <button onClick={login}>Увійти</button>
                    </div>
                </TabPanel>
                <h4>Користувач увійшов:</h4>
                {user?.email}
                <button onClick={logout} className={s.logout}> Вийти із акаунту</button>
            </Tabs>
        </div>
    );
}


export default Register;